
import { Answer, Question, QuestionService } from "@/domain";
import {
  defineComponent,
  PropType,
  onMounted,
  ref,
  watch,
  toRefs,
  computed,
} from "vue";

enum Status {
  Error = "error",
  Clean = "clean",
  Valid = "valid",
}

export default defineComponent({
  emits: ["answer"],
  props: {
    question: {
      type: Object as PropType<Question>,
      required: true,
    },
    questionNumber: { type: Number, required: true },
    challengeNumber: { type: Number, required: true },
    countryCode: { type: String, required: true },
  },
  setup: (props, context) => {
    const { questionNumber, question } = toRefs(props);

    const inputText = ref("");
    const status = ref(Status.Clean);

    const emitAnswer = () => {
      context.emit(
        "answer",
        inputText.value.trim().replace(/\s{2,}/g, " "),
        questionNumber.value
      );
    };

    const setAnswer = () => {
      const answer: Answer = QuestionService.isAnswered(question.value)
        ? question.value.answer
        : { text: "" };
      inputText.value = answer.text;

      status.value = answer.isCorrect
        ? Status.Valid
        : answer.isCorrect === false
        ? Status.Error
        : Status.Clean;
    };

    onMounted(() => setAnswer());

    watch(question, () => setAnswer());

    const imagesBucket = computed(
      () => process.env.VUE_APP_QUIZZ_RESOURCES_BUCKET
    );

    return {
      inputText,
      status,
      emitAnswer,
      imagesBucket,
    };
  },
});
